@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.6.1/font/bootstrap-icons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.basicbutton{
  cursor: pointer;
  display: inline-block;
}
.numbutton{
  width: 30%;
  margin-right: 3.33%;
  margin-bottom: 10px;
  background-color: aliceblue;
  color: black;
}
.totalnumbers{
  display: inline-block;
  width: 300px;
}
.totalope{
  width: 100px;
  display: inline-block;
}
.opebutton{
  margin-bottom: 10px;
  display: block;
  background-color: #4e5157;
  color: white ;
}
.basicbutton{
  border-radius: 6%;
  padding-top: 10px;
  padding-bottom: 10px;
}
.basicbutton:hover{
  background-color: violet;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.2s;
}
.dotbutton{
  background-color: #4e5157;
  color: white ;
  width: 60%;
  margin-right: 6.66%;
}
.dotbutton:hover{
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}
.lastrow{
  width: 400px;
  text-align: left;
}
.lastrowbuttons{
  display: inline-block;
  width: 90px;
  text-align: center;
  margin-right: 10px;
}
.lastrowbuttons{
  background-color: #4e5157;
}
.lastone{
  float: right;
  width: 199px;
  margin-right: 0;
}

@media(max-width: 488px){
  .heading{
    text-align: left;
    width: 95%;
    margin-left: 5%;
  }
  .basicbutton{
    width: 40px;
  }
  .totalnumbers{
    width: 150px;
  }
  .dotbutton{
    width: 80px;
  }
  .lastrow{
    width: 200px;
    margin-right: 33px;
    
  }
  .lastrowbuttons{
    width: 40px;
    margin-right: 5px;
  }
  .lastone{
    float: right;
    width: 95px;
    margin-right: 15px;
  }
}
.resultdiv{
  margin-bottom: 15px;
  transition: 0.3s;

}
.operatestring{
  font-size: 1.3em;
  margin-left: 15px;
}
.dop{
  font-size: 0.8em;
  color: rgb(216, 203, 203);
}
.heading{
  width: 100%;
  position: absolute;
  top: 0;
  font-size: 1.2em;
  text-align: center;
}
@media(max-width: 488px){
  .heading{
    text-align: left;
    width: 95%;
    margin-left: 5%;
  }
}
